.form {
  z-index: 5;
}

.form__fields {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  margin-bottom: 32px;
}

.form__actions {
  display: flex;
  flex-flow: row nowrap;
  justify-content: flex-end;
  margin-top: 50px;
}

.form__fields .form__group {
  flex: 0 0 auto;
  width: 255px;
}

@media only screen and (max-width: 1199px) {
  .form__fields .form__group {
    width: 215px;
  }
}

@media only screen and (max-width: 991px) {
  .form__fields {
    flex-flow: column nowrap;
  }

  .form__actions {
    justify-content: flex-start;
  }

  .form__fields .form__group {
    width: 270px;
    margin-bottom: 32px;
  }

  .form__fields .form__group:last-child {
    margin-bottom: 0px;
  }

  .form__group {
    width: 270px;
  }
}